import React from 'react';
import { Link as RouterLink } from 'react-router-dom';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import { makeStyles, Theme, useScrollTrigger, AppBar } from '@material-ui/core';

const useStyle = makeStyles((theme: Theme) => ({
  root: {
    paddingLeft: "15%",
    paddingRight: "15%",
    height: "80px",
    justifyContent: "center",
    backgroundColor: theme.palette.background.default
    // backgroundColor: "#eceff1",
  },
  indicator: {
    overflow: "hidden",
    position: "fixed",
    top: "0",
    width: "100%",
    // display: "flex",
    justifyContent: "center",
    backgroundColor: "#FFFFFF",
    "& > span": {
      maxWidth: 40,
      width: "100%",
      backgroundColor: "#635ee7"
    }
  },
  mainTab: {
    textTransform: "capitalize",
    paddingLeft: 0,
    fontSize: "28px",
    // fontWeight: "bold",
  },
  subTab: {
    // fontSize: "16px",
  }
}));

export const NavigationBar: React.FunctionComponent = () => {
  const classes = useStyle();  

  return (
    <ElevationScroll>
      <AppBar className={classes.root}>
        <Tabs
          indicatorColor="primary"
          textColor="primary"
          TabIndicatorProps={{ children: <span /> }}
        >
          <Tab label="Yeqing Kong" component={RouterLink} to={"/"} className={classes.mainTab}/>
          <Tab label="Home" component={RouterLink} to={"/"} className={classes.subTab}/>
          <Tab label="Research" component={RouterLink} to={"/research"} className={classes.subTab}/>
          <Tab label="Teaching" component={RouterLink} to={"/teaching"} className={classes.subTab}/>
          <Tab label="Gallery" component={RouterLink} to={"/gallery"} className={classes.subTab}/>
        </Tabs>
      </AppBar>
    </ElevationScroll>
  )
};

const ElevationScroll = (props: {children: React.ReactElement}) => {
  const { children } = props;

  const trigger = useScrollTrigger();

  return React.cloneElement(children, {
    elevation: trigger ? 4 : 0,
  })
};

export default NavigationBar;