import React from 'react';
import makeStyles from '@material-ui/core/styles/makeStyles';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import Button from '@material-ui/core/Button';
import DescriptionIcon from '@material-ui/icons/Description';

// import cv from '../files/cv.pdf';

const shortBioText1: string = `Yeqing Kong is a Ph.D. candidate in the program of `;
const shortBioLink1 = {
  link: "https://crdm.chass.ncsu.edu/",
  text: "Communication, Rhetoric, and Digital Media",
};
const shortBioText2: string = ` at North Carolina State University. Her research and teaching interests include international professoinal and technical communication, risk communication, intercultural communication, digital rhetoric, visual rhetoric, social justice, and corpus-assisted discourse analysis. Her scholarly work has appeared in connexions in international professional communication and design, China Media Research, and Proceedings of the ACM International Conference on the Design of Communication.`;

const useStyle = makeStyles({
  root: {
    // marginLeft: "5%",
    // background: "white",
    // boxShadow: "10 10 5 #666",
    // paddingLeft: "5%", 
    // paddingRight: "5%",
    // paddingTop: "50px",
    // paddingBottom: "50px"
  },
  title: {
    padding: "10px 0px",
  },
  body: {
    textAlign: "justify",
  }
});

const Biography: React.FunctionComponent = () => {
  const classes = useStyle();
  return (
    <>
      <Typography variant="h6" className={classes.title}>
        Biography
      </Typography>
      <Typography gutterBottom variant="body1" className={classes.body}>
        {shortBioText1}
        <Link href={shortBioLink1.link}>{shortBioLink1.text}</Link>
        {shortBioText2}
      </Typography>
      <Typography>
        <Button 
          // size="small" 
          variant="outlined" 
          color="primary" 
          startIcon={<DescriptionIcon />}
          href={"../files/cv.pdf"}
          target="_blank"
          rel="noreferrer"
        >
          View CV
        </Button>
      </Typography>
    </>
  )
}

export default Biography;