import React from 'react';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import CardHeader from '@material-ui/core/CardHeader';
import Card from '@material-ui/core/Card';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Chip from '@material-ui/core/Chip';

import ncsu from '../img/ncsu_logo.png';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      backgroundColor: theme.palette.background.paper,
      paddingLeft: "15%", 
      paddingRight: "15%",
      paddingTop: "5%",
      paddingBottom: "5%"
    },
    card: {

    },
    avatar: {

    },
    media: {

    },
    // list: {
    //   backgroundColor: theme.palette.background.paper,
    // },
    // title: {
    //   paddingTop: "20px",
    //   paddingBottom: "20px",
    // },
    // listItem: {
    //   padding: "22px 16px",
    // },
    spaceBetween: {
      display: "flex", 
      justifyContent: "space-between"
    },
    zeroFlexBasis: {
      flexBasis: "0px"
    }
  }),
);

type CoursesProps = {
  items: CourseItemPorps[];
}

type CourseItemPorps = {
  course: string,
  time: string,
  program: string,
  university: string,
  description: string,
  role: string,
  syllabus: string,
  assignment: string,
}

const Courses: React.FunctionComponent<CoursesProps> = (props: CoursesProps) => {
  const classes = useStyles();
  const { items } = props;

  return (
    <>
      <Divider></Divider>
      <div className={classes.root}>
        <Typography gutterBottom variant="h5">
          Courses
        </Typography>
        <Grid container spacing={5}>
          {items.map((item, index) => <CourseItem {...item} />)}
        </Grid>
      </div>
    </>
  )
};

export default Courses;

const CourseItem: React.FunctionComponent<CourseItemPorps> = (props: CourseItemPorps) => {
  const classes = useStyles();
  const { course, time, program, description, role } = props;

  return (
    <Grid item md={6}>
      <Card className={classes.card}>
        <CardHeader
          avatar={
            <Avatar src={ncsu} className={classes.avatar}/>
          }
          title={
            <React.Fragment>
              <Typography variant="subtitle1" color="textPrimary">
                {course}
              </Typography>
              <Typography variant="subtitle2" color="textPrimary">
                {program}
              </Typography>
            </React.Fragment>
          }
          subheader={
            <React.Fragment>
            <Grid container className={classes.spaceBetween}>
              <Grid item lg={7}>
                <Typography variant="subtitle1" color="textSecondary">
                  {time}
                </Typography>
              </Grid>
              <Grid item lg={5} className={classes.zeroFlexBasis}>
                <Chip size="small" variant="outlined" color="primary" label={role}/>
              </Grid>
            </Grid>
            </React.Fragment>
          }
        />
        <CardMedia
          className={classes.media}
          image="/static/images/cards/paella.jpg"
          title="Paella dish"
        />
        <CardContent>
          <Typography variant="body2" color="textSecondary" component="p">
            {description}
          </Typography>
        </CardContent>
        <CardActions disableSpacing>
          <IconButton aria-label="add to favorites">
            {/* <FavoriteIcon /> */}
          </IconButton>
          <IconButton aria-label="share">
            {/* <ShareIcon /> */}
          </IconButton>
          {/* <IconButton
            className={clsx(classes.expand, {
              [classes.expandOpen]: expanded,
            })}
            onClick={handleExpandClick}
            aria-expanded={expanded}
            aria-label="show more"
          >
            <ExpandMoreIcon />
          </IconButton> */}
        </CardActions>
        {/* <Collapse in={expanded} timeout="auto" unmountOnExit>
          <CardContent>
            <Typography paragraph>Method:</Typography>
            <Typography paragraph>
              Heat 1/2 cup of the broth in a pot until simmering, add saffron and set aside for 10
              minutes.
            </Typography>
            <Typography paragraph>
              Heat oil in a (14- to 16-inch) paella pan or a large, deep skillet over medium-high
              heat. Add chicken, shrimp and chorizo, and cook, stirring occasionally until lightly
              browned, 6 to 8 minutes. Transfer shrimp to a large plate and set aside, leaving chicken
              and chorizo in the pan. Add pimentón, bay leaves, garlic, tomatoes, onion, salt and
              pepper, and cook, stirring often until thickened and fragrant, about 10 minutes. Add
              saffron broth and remaining 4 1/2 cups chicken broth; bring to a boil.
            </Typography>
            <Typography paragraph>
              Add rice and stir very gently to distribute. Top with artichokes and peppers, and cook
              without stirring, until most of the liquid is absorbed, 15 to 18 minutes. Reduce heat to
              medium-low, add reserved shrimp and mussels, tucking them down into the rice, and cook
              again without stirring, until mussels have opened and rice is just tender, 5 to 7
              minutes more. (Discard any mussels that don’t open.)
            </Typography>
            <Typography>
              Set aside off of the heat to let rest for 10 minutes, and then serve.
            </Typography>
          </CardContent>
        </Collapse> */}
      </Card>
    </Grid>
  )
};