import React from 'react';
import { Paper, Chip, makeStyles, Theme, createStyles, Typography } from '@material-ui/core';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      backgroundColor: theme.palette.background.paper,
      paddingLeft: "15%", 
      paddingRight: "15%",
      paddingTop: "5%",
      paddingBottom: "5%"
    },
    paper: {
      padding: "10px",
    },
    chipArray: {
      display: 'flex',
      flexWrap: 'wrap',
      listStyle: 'none',
      padding: theme.spacing(0.5),
      margin: 0,
    },
    chip: {
      cursor: "default",
      margin: theme.spacing(0.5),
    },
  }),
);

const TeachingInterests = (props: {interests: string[]}) => {
  const { interests } = props;
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Paper elevation={0} variant="outlined" className={classes.paper}>
        <Typography gutterBottom variant="h6">
          Teaching Interests
        </Typography>
        <ul className={classes.chipArray}>
          {interests.map((interest, index) => {
            return (
              <li key={index}>
                <Chip label={interest} clickable className={classes.chip}/>
              </li>
            )
          })}
        </ul>
      </Paper>
    </div>
  )
}

export default TeachingInterests;