import React from 'react';
import { makeStyles, Theme, createStyles, Box, Grid } from '@material-ui/core';
import GalleryColumn, { GalleryItem } from './GalleryColumn';

import galleryData from '../data/gallery.json';

import img1 from '../img/gallery/1.jpg';
import img2 from '../img/gallery/2.jpg';
import img3 from '../img/gallery/3.jpg';
import img4 from '../img/gallery/4.jpg';
import img5 from '../img/gallery/5.jpg';
import img6 from '../img/gallery/6.jpg';
import img7 from '../img/gallery/7.jpg';
import img8 from '../img/gallery/8.jpg';
import img9 from '../img/gallery/9.jpg';
import img10 from '../img/gallery/10.jpg';
import img11 from '../img/gallery/11.jpg';
import img12 from '../img/gallery/12.jpg';
import img13 from '../img/gallery/13.jpg';
import img14 from '../img/gallery/14.jpg';
import img15 from '../img/gallery/15.jpg';
import img16 from '../img/gallery/16.jpg';
import img17 from '../img/gallery/17.jpg';
import img18 from '../img/gallery/18.jpg';
import img19 from '../img/gallery/19.jpg';

const galleryImages: string[] = [
  img1, img2, img3, img4, img5, img6, img7, img8, img9, img10, 
  img11, img12, img13, img14, img15, img16, img17, img18, img19
]

const useStyle = makeStyles((theme: Theme) => 
  createStyles({
    root: {
      marginTop: "80px",
      paddingLeft: "15%",
      paddingRight: "15%",
      paddingTop: "50px",
      paddingBottom: "50px",
    },
  })
);

const GalleryPage: React.FunctionComponent = () => {
  const classes = useStyle();

  React.useEffect(() => {
    window.scrollTo(0, 0)
  }, [])

  return (
    <div className={classes.root}>
      <Box display={{xs: "none", sm: "none", md: "none", lg: "block", xl: "none"}}>
        <Grid container justify="center">
          <GalleryColumn items={generateGalleryItems([1, 5, 9, 13, 16])} lg={4}/>
          <GalleryColumn items={generateGalleryItems([2, 6, 10, 14, 17])} lg={4}/>
          <GalleryColumn items={generateGalleryItems([3, 7, 11, 15, 18, 19])} lg={4}/>
        </Grid>
      </Box>
      <Box display={{xs:"block", sm: "block", md: "block", lg: "none", xl: "block"}}>
        <Grid container justify="center">
          <GalleryColumn items={generateGalleryItems([1, 5, 9, 13, 17])} xl={3} md={6} sm={6} xs={12}/>
          <GalleryColumn items={generateGalleryItems([2, 6, 10, 14, 18])} xl={3} md={6} sm={6} xs={12}/>
          <GalleryColumn items={generateGalleryItems([3, 7, 11, 15, 19])} xl={3} md={6} sm={6} xs={12}/>
          <GalleryColumn items={generateGalleryItems([4, 8, 12, 16])} xl={3} md={6} sm={6} xs={12}/>
        </Grid>
      </Box>
    </div>
  )
}

export default GalleryPage;

function generateGalleryItems(indices: number[]): GalleryItem[] {
  const items: GalleryItem[] = indices.map((index) => {
    return {
      path: galleryImages[index-1],
      description: galleryData[index-1].description,
    } as GalleryItem;
  });
  return items;
}
